import React, { Component } from "react"
import "./App.css"

class App extends Component {
	state = {
		creditValue: null,
		RPO: null,
		ASS: null,
		result: null,
		payment: 3.3,
	}

	handleCredit = (e) => {				
		this.setState({
			creditValue: e.target.value,
		})
	}


	handleRPO = (e)=>{
		this.setState({
			RPO: e.target.value,
		})
	}

	handleASS = (e)=>{
		this.setState({
			ASS: e.target.value,
		})
	}

	calculate = (e)=>{
		const {creditValue, RPO, ASS, result, payment} = this.state
		const newRPO = parseInt(RPO)
		const newASS = parseInt(ASS)
	
		
		const finalResult = ((creditValue*newRPO+newASS)/1000)*payment
		console.log(finalResult)
		this.setState({
			result: finalResult.toFixed(2)+'zł',
		})

	}

	render() {
		return (
			<>
				<div>
					<h2>Zarobisz: <span>{this.state.result}</span> </h2>
				</div>

				<div className="App">
					<div>
						<form action="">
							<label htmlFor="credit-value">
								Wartość kredytu:
								<input onChange={this.handleCredit} className="credit" id="credit-value" type="number" placeholder="Wpisz wartość kredytu" />
							</label>
						</form>
						<p className="error"></p>
					</div>

					<div>
						<form action="">
							<label htmlFor="rpo">
								Sprzedałeś RPO?
								<p>
									<input onChange={this.handleRPO} id="rpo" type="radio" name="plec" value={2} /> Nie
								</p>
								<p>
									<input onChange={this.handleRPO} id="rpo" type="radio" name="plec" value="4" /> Tak
								</p>
							</label>
						</form>
					</div>
					<div>
						<form action="">
							<label htmlFor="ass">
								Sprzedałeś Ass'a?
								<p>
									<input onChange={this.handleASS} id="ass" type="radio" name="plec" value="0" /> Nie
								</p>
								<p>
									<input onChange={this.handleASS} id="ass" type="radio" name="plec" value={2000} /> ASS1
								</p>
								<p>
									<input onChange={this.handleASS} id="ass" type="radio" name="plec" value="3000" /> ASS3
								</p>
							</label>
						</form>
					</div>
				</div>
				<button onClick={this.calculate}>Oblicz</button>
			</>
		)
	}
}

export default App
